import _ from 'lodash';
import * as Sentry from '@sentry/browser';

import { log } from 'ts/scripts/logger';
import { importMessages, Locales } from 'ts/helpers';

import { IAds, initialState as adsInitialState } from './ads/reducer';
import { IAppSettings, initialState as settingsInitialState } from './settings/reducer';

type OnSuccess = () => void;
type OnError = (e: Error) => void;

export interface IAppState {
    ads: IAds;
    settings: IAppSettings;
}

export const loadState = (callback: (state: IAppState | undefined) => void) => {
    try {
        chrome.storage.local.get(['appstate'], (result) => {
            const { appstate } = result;
            if (!appstate) {
                const navigatorLanguage = navigator?.language?.split('-')?.[0];
                const locale =
                    Object.keys(Locales).indexOf(navigatorLanguage) > -1
                        ? Locales[navigatorLanguage as Locales]
                        : Locales.en;
                callback({
                    ads: {
                        ...adsInitialState,
                    },
                    settings: {
                        ...settingsInitialState,
                        locale,
                        messages: importMessages(locale),
                    },
                });
            } else {
                callback({ ...appstate });
            }
        });
    } catch (err) {
        Sentry.captureException(err);
        log(err, 'loadState');
        callback(undefined);
    }
};

export const saveState = (
    appstate: IAppState,
    // tslint:disable-next-line: no-empty
    success: OnSuccess = () => {},
    // tslint:disable-next-line: no-empty
    error: OnError = () => {},
) => {
    try {
        chrome.storage.local.set({ appstate }, success);
    } catch (err) {
        Sentry.captureException(err);
        log(err, 'saveState');
        if (err instanceof Error) {
            error(err);
        }
    }
};
