import { configureStore } from '@reduxjs/toolkit';
import { createWrapStore } from 'webext-redux';
import { onInstallListener } from 'ts/scripts/utils';
import {
    BrowserClient,
    defaultStackParser,
    getDefaultIntegrations,
    makeFetchTransport,
    Scope,
} from '@sentry/browser';

import { configureApp } from './AppConfig';
import adsReducer from './store/ads/reducer';
import settingsReducer from './store/settings/reducer';
import { IAppState, loadState } from './store';
import { listenerMiddleware } from './store/listenerMiddleware';

const manifest = chrome.runtime.getManifest();

const wrapStore = createWrapStore();

const integrations = getDefaultIntegrations({}).filter((defaultIntegration) => {
    return !['BrowserApiErrors', 'TryCatch', 'Breadcrumbs', 'GlobalHandlers'].includes(
        defaultIntegration.name,
    );
});

const client = new BrowserClient({
    dsn: 'https://2d960f7bacd640348d65a3db92124222@o531045.ingest.sentry.io/5651244',
    transport: makeFetchTransport,
    stackParser: defaultStackParser,
    integrations,
    release: manifest.version,
});

const scope = new Scope();
scope.setClient(client);

if (process.env.NODE_ENV !== 'development') {
    chrome.runtime.onInstalled.addListener(onInstallListener);
}

chrome.runtime.onMessage.addListener((request, _sender, sendResponse) => {
    try {
        if (request.type === 'get-state') {
            chrome.storage.local.get('appstate', (state) => {
                sendResponse(state.appstate);
            });
        }
    } catch {
        sendResponse(null);
    }
    return true;
});

loadState((preloadedState: IAppState | undefined) => {
    const store = configureStore({
        preloadedState,
        reducer: {
            ads: adsReducer,
            settings: settingsReducer,
        },
        middleware: (getDefaultMiddleware) =>
            getDefaultMiddleware().prepend(listenerMiddleware.middleware),
    });

    configureApp(store);
    wrapStore(store);
});
