export const exportAsFile = (dataToExport: ExportAd[], fileName: string) => {
    const fileType =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const ws = XLSX.utils.json_to_sheet(dataToExport);
    const wb = { Sheets: { MYADFINDER: ws }, SheetNames: ['MYADFINDER'] };
    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, fileName + '.xlsx');
};

export const buildExportedData = (ads: IAd[]): ExportAd[] => {
    const newAds = ads.map((ad) => {
        const Likes = ad.likes;
        const Like = ad.like;
        const Sorry = ad.sorry;
        const Anger = ad.anger;
        const Love = ad.love;
        const Wow = ad.wow;
        const Haha = ad.haha;
        const Comments = ad.commentsCount;
        const Shares = ad.shareCount;
        const Views = ad.viewsCount ? ad.viewsCount : '';
        const Pagelink = ad.pageUrl;
        const PageName = ad.pageName;
        const Text = ad.postText;
        const Type = ad.postType;
        const Postlink = `${ad.pageId}/posts/${ad.postId}`;
        return {
            Postlink,
            Type,
            Text,
            PageName,
            Pagelink,
            Likes,
            Like,
            Sorry,
            Anger,
            Love,
            Wow,
            Haha,
            Comments,
            Shares,
            Views,
        };
    });
    return newAds;
};

export const randomInteger = (min: number, max: number): number => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
};

export const refreshFacebookTabs = () => {
    chrome.tabs.query({ url: '*://*.facebook.com/*' }, (tabs) => {
        tabs.forEach((tab) => {
            if (tab.id) {
                chrome.tabs.reload(tab.id);
            }
        });
    });
};

export const onInstallListener = () => {
    chrome.tabs.create(
        {
            url: 'https://www.facebook.com/',
        },
        () => log('new facebook tab'),
    );
};

export const formatUrl = (url: string) => {
    try {
        if (!url) {
            return url;
        }
        return decodeURIComponent(url.replace('https://l.facebook.com/l.php?u=', ''));
    } catch (error) {
        return url;
    }
};

export const computeRatio = (width: number, height: number) => {
    return height / width;
};

export const parseAds = (posts: any[]): IAd[] =>
    posts
        .filter(({ data }) => JSON.stringify(data).includes('SponsoredData'))
        .filter((d) => {
            if (
                !d?.data?.node?.comet_sections &&
                !d?.data?.viewer?.news_feed?.edges?.[0]?.node?.comet_sections
            ) {
                warn(d, 'invalid post data');
                return false;
            }
            return true;
        })
        .map((data) => {
            return buildAdDataObject(
                data?.data?.viewer?.news_feed?.edges?.[0]?.node?.comet_sections ||
                    data.data.node.comet_sections,
            );
        });

export const getInitialData = (body: HTMLElement) => {
    return Array.from(body.querySelectorAll('script:not([src])'))
        .map((script) => script.innerHTML)
        .filter(
            (script) =>
                (script.includes('requireLazy(["Bootloader"]') && script.includes('"node"')) ||
                script.includes('"SPONSORED"'),
        )
        .map((script) => {
            const n = '{"__bbox":{"complete.*?"result":{';
            const openingBracketIndex = script.search(n);
            try {
                const closingBracketIndex = findClosingBracketMatchIndex(
                    script,
                    openingBracketIndex,
                );
                const s = script.slice(openingBracketIndex, closingBracketIndex) + '}';
                return JSON.parse(s).__bbox.result;
            } catch (err) {
                log(err, 'getInitialData');
                return [];
            }
        })
        .filter(
            ({ label }) => 'CometNewsFeed_viewer$stream$CometNewsFeed_viewer_news_feed' === label,
        );
};

export const findClosingBracketMatchIndex = (script: string, openingBrackedIndex: number) => {
    if ('{' !== script[openingBrackedIndex]) {
        throw new Error("No '{' at index " + openingBrackedIndex);
    }

    for (let n = 1, r = openingBrackedIndex + 1; r < script.length; r++) {
        switch (script[r]) {
            case '{':
                n++;
                break;
            case '}':
                if (0 === --n) return r;
        }
    }

    return -1;
};

export const getTopLevelObjectsFromString = (originScript: string) => {
    const objects = [];
    let script = originScript;
    for (; script.length > 0; ) {
        try {
            const openingBracketIndex = script.indexOf('{');
            if (-1 === openingBracketIndex) break;
            const closingBracketIndex = findClosingBracketMatchIndex(script, openingBracketIndex);
            const object = script.slice(openingBracketIndex, closingBracketIndex + 1);
            const parsedJson = JSON.parse(object);
            objects.push(parsedJson);
            script = script.slice(closingBracketIndex + 1);
        } catch (err) {
            log(err, 'getTopLevelObjectsFromString');
            break;
        }
    }
    return objects;
};

export const buildAdDataObject = ({
    content: { story: postStory },
    context_layout: { story: pageStory },
    feedback: { story: feedbackStory },
}: any): IAd => {
    // Post type
    const postType = getPostType(postStory);
    // carousel info
    const carouselCardsInfos = getCarouselCardsInfos(postStory);
    // page info
    const { pageId, adLibraryId, pageName, pageLogo, pageUrl, pageCategory } =
        getPageInfo(pageStory);

    // video info
    const { videoId, videoUrl, videoHdUrl, videoHeight, videoWidth, thumbnailImage } =
        getVideoInfo(postStory);
    const publishedAt = getPublishedAt(postStory);
    // post info
    const postText = getPostText(postStory);
    const {
        uri: postImage,
        width: postImageWidth,
        height: postImageHeight,
    } = getPostImage(postStory);
    const postAlbumImages = getPostAlbum(postStory);
    // fouter info
    const callToActionType = getCallToActionType(postStory);
    const callToActionText = getCallToActionText(postStory, callToActionType);
    const { fouterTitle, fouterDescription, fouterSource, fouterUrl } = getFooterInfo(postStory);
    // reactions
    const feedbackTargetWithContext =
        feedbackStory?.story_ufi_container?.story?.feedback_context?.feedback_target_with_context ||
        feedbackStory.comet_feed_ufi_container?.story?.story_ufi_container?.story?.feedback_context
            ?.feedback_target_with_context ||
        feedbackStory.comet_feed_ufi_container?.story?.feedback_context
            ?.feedback_target_with_context ||
        feedbackStory.feedback_context?.feedback_target_with_context ||
        feedbackStory.stroy?.feedback_context?.feedback_target_with_context;

    const postId = getPostId(feedbackTargetWithContext);
    const reactionsInfo = getReactionsInfo(feedbackTargetWithContext);
    const shareCount = getShareCount(feedbackTargetWithContext);
    const commentsCount = getCommentsCount(feedbackTargetWithContext);
    const viewsCount = getVideoViewsCount(feedbackTargetWithContext);
    const gender = getActorGender(feedbackTargetWithContext);

    return {
        postData: !postId
            ? {
                  postStory,
                  pageStory,
                  feedbackStory,
              }
            : undefined,
        pageUrl,
        pageCategory,
        postId,
        postText,
        callToActionText,
        callToActionType,
        commentsCount,
        pageName,
        videoUrl,
        videoHdUrl,
        thumbnailImage,
        videoHeight,
        videoWidth,
        shareCount,
        adLibraryId,
        pageId,
        postType,
        videoId,
        publishedAt,
        carouselCardsInfos,
        postImage,
        postImageWidth,
        postImageHeight,
        pageLogo,
        postAlbumImages,
        fouterTitle,
        fouterDescription,
        fouterSource,
        fouterUrl,
        viewsCount,
        gr: gender,
        source: 'feed',
        addedAt: new Date().toISOString(),
        ...reactionsInfo,
    };
};

const getPublishedAt = (story: any): any => {
    try {
        if (!story.attachments[0]?.styles.attachment.media?.publish_time) {
            return '';
        }
        const time = story.attachments[0].styles.attachment.media.publish_time;
        return new Date(time * 1000).toISOString();
    } catch (err) {
        log(err, 'getPublishedAt');
        return '';
    }
};

const getReactionsInfo = (target: any): IReaction => {
    try {
        const summaryAndActions =
            target.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer ||
            target?.comet_ufi_summary_and_actions_renderer;
        if (!summaryAndActions) {
            return {
                like: 0,
                anger: 0,
                love: 0,
                likes: 0,
                wow: 0,
                support: 0,
                haha: 0,
                sorry: 0,
            };
        }
        const reactionsData =
            summaryAndActions.feedback.top_reactions?.edges ||
            summaryAndActions.feedback.cannot_see_top_custom_reactions?.top_reactions?.edges ||
            [];
        return reactionsData.reduce(
            (map: { [key: string]: number }, reaction: any) => ({
                ...map,
                likes: map.likes + reaction.reaction_count,
                [getReactionKey(reaction.node.id)]: reaction.reaction_count,
            }),
            {
                like: 0,
                anger: 0,
                love: 0,
                likes: 0,
                wow: 0,
                support: 0,
                haha: 0,
                sorry: 0,
            },
        );
    } catch (err) {
        log(err, 'getReactionsInfo');
        return {
            like: 0,
            anger: 0,
            love: 0,
            likes: 0,
            wow: 0,
            support: 0,
            haha: 0,
            sorry: 0,
        };
    }
};

const getCarouselCardsInfos = (story: any): ICarrousel[] => {
    try {
        const subattachments = story.attachments[0]?.styles.attachment.subattachments || [];
        return subattachments.map((subattachment: any) => {
            let type;
            let width;
            let height;
            let uri;
            let video: IVideo | undefined;
            if (subattachment.media) {
                const { media } = subattachment;
                if ((media.__typename || '').toLowerCase() === 'video') {
                    const progressiveUrls: {
                        progressive_url: string;
                        metadata: {
                            quality: 'HD' | 'SD';
                        };
                    }[] =
                        media.videoDeliveryResponseFragment?.videoDeliveryResponseResult
                            ?.progressive_urls || [];

                    const videoHdUrl = progressiveUrls.find(
                        (item) => item?.metadata?.quality === 'HD',
                    )?.progressive_url;

                    const videoUrl = progressiveUrls.find(
                        (item) => item?.metadata?.quality === 'SD',
                    )?.progressive_url;
                    type = PostType.VIDEO;
                    video = {
                        videoId: media.id,
                        videoHdUrl: videoHdUrl || media.playable_url_quality_hd,
                        videoUrl: videoUrl || media.playable_url,
                        videoWidth: media.original_width,
                        videoHeight: media.original_height,
                        thumbnailImage: media.thumbnailImage?.uri,
                    };
                } else {
                    type = PostType.PHOTO;
                    ({ width, height, uri } = subattachment.media.image);
                }
            } else if (subattachment.multi_share_media_card_renderer?.attachment?.media) {
                const { media } = subattachment.multi_share_media_card_renderer.attachment;
                if ((media.__typename || '').toLowerCase() === 'video') {
                    const progressiveUrls: {
                        progressive_url: string;
                        metadata: {
                            quality: 'HD' | 'SD';
                        };
                    }[] =
                        media.videoDeliveryResponseFragment?.videoDeliveryResponseResult
                            ?.progressive_urls || [];

                    const videoHdUrl = progressiveUrls.find(
                        (item) => item?.metadata?.quality === 'HD',
                    )?.progressive_url;

                    const videoUrl = progressiveUrls.find(
                        (item) => item?.metadata?.quality === 'SD',
                    )?.progressive_url;
                    type = PostType.VIDEO;
                    video = {
                        videoId: media.id,
                        videoHdUrl: videoHdUrl || media.playable_url_quality_hd,
                        videoUrl: videoUrl || media.playable_url,
                        videoWidth: media.original_width,
                        videoHeight: media.original_height,
                        thumbnailImage: media.thumbnailImage?.uri,
                    };
                } else if (media.image) {
                    type = PostType.PHOTO;
                    ({ width, height, uri } = media.image);
                } else if (media.slideshow_images) {
                    type = PostType.PHOTO;
                    uri = media.slideshow_images?.[0]?.uri;
                }
            }

            return {
                type,
                text: subattachment.card_title.text,
                imageUrl: uri,
                imageHeight: height,
                imageWidth: width,
                video,
                description: subattachment.card_description.text,
                actionUrl: subattachment.call_to_action_renderer?.action_link.url,
                actionTitle: subattachment.call_to_action_renderer?.action_link.title,
            };
        });
    } catch (err) {
        log(err, 'getCarouselCardsInfos');
        return [];
    }
};

const getFooterInfo = (story: any): IFooter => {
    try {
        if (!story.attachments[0]?.comet_footer_renderer) {
            return {
                fouterTitle: '',
                fouterDescription: '',
                fouterSource: '',
                fouterUrl: '',
            };
        }
        const { attachment } = story.attachments[0].comet_footer_renderer;

        let fouterTitle = attachment.title_with_entities?.text;
        let fouterDescription = attachment.description?.text;
        let fouterSource = attachment.source?.text;
        let fouterUrl =
            attachment.target?.external_url ||
            attachment.story_attachment_link_renderer?.attachment?.web_link?.url;

        if (!!fouterTitle || !!fouterDescription || !!fouterSource) {
            return {
                fouterTitle,
                fouterDescription,
                fouterSource,
                fouterUrl,
            };
        }

        fouterTitle = attachment.action_links?.[0]?.link_title;
        fouterDescription = attachment.action_links?.[0]?.link_description;
        fouterSource = attachment.action_links?.[0]?.link_display;
        fouterUrl = attachment.action_links?.[0]?.url;

        return {
            fouterTitle,
            fouterDescription,
            fouterSource,
            fouterUrl,
        };
    } catch (err) {
        log(err, 'getFooterInfo');
        return {
            fouterTitle: '',
            fouterDescription: '',
            fouterSource: '',
            fouterUrl: '',
        };
    }
};
const getPostId = (feedbackTargetWithContext: any) => {
    const methode1 = (target: any) => {
        try {
            return (
                target.top_level_comment_list_renderer?.feedback?.share_fbid ||
                target.top_level_comment_list_renderer?.feedback?.subscription_target_id
            );
        } catch (err) {
            log(err, 'getPostId/methode1');
            return undefined;
        }
    };

    const methode2 = (target: any) => {
        try {
            const { share_fbid, subscription_target_id } = target;
            return share_fbid || subscription_target_id;
        } catch (err) {
            log(err, 'getPostId/methode2');
            return undefined;
        }
    };

    const methode3 = (target: any) => {
        try {
            if (
                !target.top_level_comment_list_renderer?.feedback
                    ?.content_based_comment_list_renderer?.feedback
                    ?.mentions_datasource_js_constructor_args_json
            ) {
                return undefined;
            }

            const idToParse = JSON.parse(
                target.top_level_comment_list_renderer.feedback.content_based_comment_list_renderer
                    .feedback.mentions_datasource_js_constructor_args_json,
            );

            if (!idToParse) {
                return undefined;
            }

            return idToParse[0].queryData.post_fbid;
        } catch (err) {
            log(err, 'getPostId/methode3');
            return undefined;
        }
    };

    const methode4 = (target: any) => {
        try {
            const share_fbid = target.ufi_renderer?.feedback?.share_fbid;
            const subscription_target_id = target.ufi_renderer?.feedback?.subscription_target_id;
            return share_fbid || subscription_target_id;
        } catch (err) {
            log(err, 'getPostId/methode4');
            return undefined;
        }
    };

    const methode5 = (target: any) => {
        try {
            const { comet_ufi_summary_and_actions_renderer } = target;
            return comet_ufi_summary_and_actions_renderer?.feedback?.subscription_target_id;
        } catch (err) {
            log(err, 'getPostId/methode5');
            return undefined;
        }
    };

    const methode6 = (target: any) => {
        try {
            return target.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer?.feedback
                ?.subscription_target_id;
        } catch (err) {
            log(err, 'getPostId/methode6');
            return undefined;
        }
    };

    try {
        if (methode6(feedbackTargetWithContext)) {
            return methode6(feedbackTargetWithContext);
        } else if (methode4(feedbackTargetWithContext)) {
            return methode4(feedbackTargetWithContext);
        } else if (methode1(feedbackTargetWithContext)) {
            return methode1(feedbackTargetWithContext);
        } else if (methode2(feedbackTargetWithContext)) {
            return methode2(feedbackTargetWithContext);
        } else if (methode3(feedbackTargetWithContext)) {
            return methode3(feedbackTargetWithContext);
        } else if (methode5(feedbackTargetWithContext)) {
            return methode5(feedbackTargetWithContext);
        }
        return null;
    } catch (err) {
        log(err, 'getPostId');
        return null;
    }
};

const getPostText = (story: any) => {
    try {
        if (!story.comet_sections.message) {
            return undefined;
        }
        return story.comet_sections.message.story.message.text;
    } catch (err) {
        log(err, 'getPostText');
        return undefined;
    }
};

const getCallToActionText = (story: any, callToActionType: any) => {
    if (callToActionType === PageActions.Like) {
        return 'Like Page';
    }

    if (callToActionType === PageActions.Message) {
        return 'Send Message';
    }

    if (callToActionType === PageActions.Follow) {
        return 'Follow';
    }

    const getOpenActionLinkTitle = (t: any) => t.title || t.link_title;

    const methode1 = (attachment: any) => {
        try {
            if (!attachment.comet_footer_renderer?.attachment?.call_to_action_renderer) {
                return undefined;
            }

            const { action_link } =
                attachment.comet_footer_renderer.attachment.call_to_action_renderer;
            return getOpenActionLinkTitle(action_link);
        } catch (err) {
            log(err, 'getCallToActionText/methode1');
            return undefined;
        }
    };

    const methode2 = (attachment: any) => {
        try {
            if (!attachment.styles.attachment.cta_screen_renderer) {
                return undefined;
            }

            const { action_link } = attachment.styles.attachment.cta_screen_renderer;
            return getOpenActionLinkTitle(action_link);
        } catch (err) {
            log(err, 'getCallToActionText/methode2');
            return undefined;
        }
    };

    const methode3 = (attachment: any) => {
        try {
            if (!attachment.styles.attachment.subattachments?.[0]?.call_to_action_renderer) {
                return undefined;
            }

            const { action_link } =
                attachment.styles.attachment.subattachments[0].call_to_action_renderer;
            return getOpenActionLinkTitle(action_link);
        } catch (err) {
            log(err, 'getCallToActionText/methode3');
            return undefined;
        }
    };

    try {
        const attachment = story.attachments[0];
        if (!attachment) {
            return null;
        } else if (methode1(attachment)) {
            return methode1(attachment);
        } else if (methode2(attachment)) {
            return methode2(attachment);
        } else if (methode3(attachment)) {
            return methode3(attachment);
        }
        return null;
    } catch (err) {
        log(err, 'getCallToActionText');
        return null;
    }
};

