const getCommentsCount = (target: any) => {
    try {
        if (target?.comment_rendering_instance?.comments?.total_count) {
            return target.comment_rendering_instance.comments.total_count;
        }

        if (target?.ufi_renderer?.feedback?.total_comment_count) {
            return target.ufi_renderer.feedback.total_comment_count;
        }

        if (
            target?.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer?.feedback
                ?.comments_count_summary_renderer?.feedback?.comment_rendering_instance?.comments
                ?.total_count
        ) {
            return target.ufi_renderer.feedback.comet_ufi_summary_and_actions_renderer.feedback
                .comments_count_summary_renderer.feedback.comment_rendering_instance.comments
                .total_count;
        }

        const commentCount =
            target?.ufi_renderer?.feedback?.comment_rendering_instance?.comments ||
            target?.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer?.feedback
                ?.comments_count_summary_renderer?.feedback?.comment_count ||
            target?.ufi_renderer?.feedback?.comment_count ||
            target?.ufi_renderer?.feedback?.comment_list_renderer?.feedback?.comment_count ||
            target?.comment_count;
        return commentCount?.total_count || 0;
    } catch (err) {
        log(err, 'getCommentsCount');
        return 0;
    }
};

const getActorGender = (target: any) => {
    try {
        return target?.ufi_renderer?.feedback?.viewer_actor?.gender?.toLowerCase() === 'male'
            ? 1
            : 2;
    } catch (err) {
        log(err, 'getActorGender');
        return 0;
    }
};

const getPageInfo = (data: any): IPage => {
    try {
        const {
            comet_sections: { title, actor_photo },
        } = data;
        const { name: pageName, id: pageId, category_type: pageCategory } = title.story.actors[0];
        const actor = actor_photo.story.actors[0];
        const { uri: pageLogo } = actor.profile_picture;
        const { url: pageUrl } = actor;
        const adLibraryId = actor.delegate_page?.id;
        return {
            pageName,
            adLibraryId,
            pageId,
            pageLogo,
            pageUrl,
            pageCategory,
        };
    } catch (err) {
        log(err, 'getPageInfo');
        return {
            pageName: '',
            pageId: '',
            adLibraryId: '',
            pageLogo: '',
            pageUrl: '',
            pageCategory: '',
        };
    }
};

interface IImage {
    uri: string;
    width: number;
    height: number;
}

const getPostImage = (story: any): IImage => {
    try {
        let media = story.attachments[0]?.styles.attachment.media;
        if (!media && story.attached_story) {
            ({ media } = story.attached_story.attachments[0].styles.attachment);
        }
        return getImageUri(media);
    } catch (err) {
        log(err, 'getPostImage');
        return {
            uri: '',
            width: 0,
            height: 0,
        };
    }
};

const getImageUri = (media: any): IImage => {
    if (!media) {
        return {
            uri: '',
            width: 0,
            height: 0,
        };
    }
    if (media.flexible_height_share_image) {
        const { uri, width, height } = media.flexible_height_share_image;
        return { uri, width, height };
    } else if (media.large_share_image) {
        const { uri, width, height } = media.large_share_image;
        return { uri, width, height };
    } else if (media.card_image) {
        const { uri, width, height } = media.card_image;
        return { uri, width, height };
    } else if (media.image) {
        const { uri, width, height } = media.image;
        return { uri, width, height };
    } else if (media.photo_image) {
        const { uri, width, height } = media.photo_image;
        return { uri, width, height };
    }
    return {
        uri: '',
        width: 0,
        height: 0,
    };
};

const getPostAlbum = (story: any) => {
    try {
        const allSubattachmentsNodes =
            story.attachments[0]?.styles.attachment.all_subattachments?.nodes || [];

        return allSubattachmentsNodes.map((node: any) => node.media.image.uri);
    } catch (err) {
        log(err, 'getPostAlbum');
        return [];
    }
};

const getShareCount = (target: any) => {
    try {
        const summaryAndActions =
            target.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer ||
            target.comet_ufi_summary_and_actions_renderer;
        if (!summaryAndActions) {
            return 0;
        }
        return summaryAndActions.feedback.share_count.count;
    } catch (err) {
        log(err, 'getShareCount');
        return 0;
    }
};

const getVideoViewsCount = (target: any) => {
    try {
        return (
            target?.video_view_count ||
            target?.comet_ufi_summary_and_actions_renderer?.feedback?.video_view_count ||
            target?.ufi_renderer?.feedback?.comet_ufi_summary_and_actions_renderer?.feedback
                ?.video_view_count ||
            0
        );
    } catch (err) {
        log(err, 'getVideoViewsCount');
        return 0;
    }
};

const getPostType = (story: any) => {
    const { attachments } = story;

    if (isEmpty(attachments) && isEmpty(story.attached_story?.attachments)) {
        return PostType.TEXT;
    }

    let renderer = attachments[0].styles;

    if (!renderer) {
        renderer = story.attached_story.attachments[0].styles;
    }

    switch (renderer.__typename) {
        case 'StoryAttachmentVideoStyleRenderer':
            return PostType.VIDEO;
        case 'StoryAttachmentAlbumStyleRenderer':
            return PostType.ALBUM;
        case 'StoryAttachmentShareAdStyleRender':
        case 'StoryAttachmentPhotoStyleRenderer':
            return PostType.PHOTO;
        case 'StoryAttachmentMultiShareStyleRenderer':
            return PostType.CAROUSEL;
        case 'StoryAttachmentLeadGenLinkRenderer':
            return PostType.LINK;
        default:
            return PostType.UNKNOWN;
    }
};

const getVideoInfo = (story: any): IVideo => {
    try {
        const media = story.attachments[0]?.styles.attachment.media;
        if (!media) {
            return {
                videoHdUrl: '',
                videoId: '',
                videoUrl: '',
                videoWidth: 0,
                videoHeight: 0,
                thumbnailImage: '',
            };
        }

        const progressiveUrls: {
            progressive_url: string;
            metadata: {
                quality: 'HD' | 'SD';
            };
        }[] =
            media.videoDeliveryResponseFragment?.videoDeliveryResponseResult?.progressive_urls ||
            [];

        const videoHdUrl = progressiveUrls.find(
            (item) => item?.metadata?.quality === 'HD',
        )?.progressive_url;

        const videoUrl = progressiveUrls.find(
            (item) => item?.metadata?.quality === 'SD',
        )?.progressive_url;

        return {
            videoHdUrl: videoHdUrl || media.playable_url_quality_hd || media.browser_native_hd_url,
            videoId: media.id,
            videoUrl: videoUrl || media.playable_url || media.browser_native_sd_url,
            videoWidth: media.original_width,
            videoHeight: media.original_height,
            thumbnailImage: media.thumbnailImage?.uri,
        };
    } catch (err) {
        log(err, 'getVideoInfo');
        return {
            videoHdUrl: '',
            videoId: '',
            videoUrl: '',
            videoWidth: 0,
            videoHeight: 0,
            thumbnailImage: '',
        };
    }
};

export const parseWatchAds = (posts: any[]): IAd[] =>
    posts
        .map((post) => {
            let feedUnit = post.data?.node?.feed_unit;
            if (!feedUnit) {
                feedUnit = post.data?.node?.section_components?.edges?.[0]?.node?.feed_unit;
            }
            return feedUnit;
        })
        .filter((feedUnit: any) => !isNil(feedUnit?.sponsored_data))
        .map((feedUnit) => buildWatchAdDataObject(feedUnit));

export const buildWatchAdDataObject = (postData: any): IAd => {
    const publishedAt = getWatchPublishedAt(postData);
    // Post type
    const postType = getWatchPostType(postData);
    // page info
    const { pageId, adLibraryId, pageName, pageLogo, pageUrl, pageCategory } =
        getWatchPageInfo(postData);
    // video info
    const { videoId, videoUrl, videoHdUrl, videoHeight, videoWidth, thumbnailImage } =
        getWatchVideoInfo(postData);

    // fouter info
    const callToActionType = getWatchCallToActionType(postData);
    const callToActionText = getWatchCallToActionText(postData);
    const { fouterTitle, fouterDescription, fouterSource, fouterUrl } =
        getWatchFouterInfo(postData);

    // post info
    const postId = getWatchPostId(postData);
    const postText = getWatchPostText(postData);
    const {
        uri: postImage,
        width: postImageWidth,
        height: postImageHeight,
    } = getWatchPostImage(postData);

    // reactions
    const reactionsInfo = getWatchReactionsInfo(postData);
    const commentsCount = getWatchCommentsCount(postData);
    const viewsCount = getWatchViewsCount(postData);

    // not available for the mement
    const shareCount = 0;

    return {
        postData: !postId ? postData : undefined,
        pageUrl,
        pageCategory,
        postId,
        postText,
        callToActionText,
        callToActionType,
        commentsCount,
        pageName,
        videoUrl,
        videoHdUrl,
        videoHeight,
        videoWidth,
        thumbnailImage,
        publishedAt,
        shareCount,
        adLibraryId,
        pageId,
        postType,
        videoId,
        carouselCardsInfos: [],
        postImage,
        postImageWidth,
        postImageHeight,
        pageLogo,
        postAlbumImages: [],
        fouterTitle,
        fouterDescription,
        fouterSource,
        fouterUrl,
        viewsCount,
        source: 'watch',
        addedAt: new Date().toISOString(),
        ...reactionsInfo,
    };
};

const getWatchPostId = (data: any) => {
    try {
        const tracking = JSON.parse(data.tracking);
        return tracking.top_level_post_id;
    } catch (err) {
        log(err, 'getWatchPostId');
        return null;
    }
};

const getWatchPageInfo = (data: any): IPage => {
    try {
        const [actor] = data.actors;
        const pageId = actor.id;
        const pageName = actor.name;
        const pageLogo = actor.profile_picture.uri;
        const pageUrl = `https://www.facebook.com/${pageId}`;
        const pageCategory = '';

        return {
            pageName,
            pageId,
            adLibraryId: '',
            pageLogo,
            pageUrl,
            pageCategory,
        };
    } catch (err) {
        log(err, 'getWatchPageInfo');
        return {
            pageName: '',
            pageId: '',
            adLibraryId: '',
            pageLogo: '',
            pageUrl: '',
            pageCategory: '',
        };
    }
};

const getWatchVideoInfo = (data: any): IVideo => {
    try {
        const media = data.attachments?.[0]?.media;
        if (!media) {
            return {
                videoHdUrl: '',
                videoId: '',
                videoUrl: '',
                videoWidth: 0,
                videoHeight: 0,
                thumbnailImage: '',
            };
        }

        return {
            videoHdUrl: media.playable_url_quality_hd || media.browser_native_hd_url,
            videoId: media.id,
            videoUrl: media.playable_url || media.browser_native_sd_url,
            videoWidth: media.original_width,
            videoHeight: media.original_height,
            thumbnailImage: media.thumbnailImage?.uri,
        };
    } catch (err) {
        log(err, 'getWatchVideoInfo');
        return {
            videoHdUrl: '',
            videoId: '',
            videoUrl: '',
            videoWidth: 0,
            videoHeight: 0,
            thumbnailImage: '',
        };
    }
};

const getWatchPublishedAt = (data: any): string => {
    try {
        const media = data.attachments?.[0]?.media;
        if (!media) {
            return '';
        }
        return media?.publish_time ? new Date(media.publish_time * 1000).toISOString() : '';
    } catch (err) {
        log(err, 'getWatchPublishedAt');
        return '';
    }
};

const getWatchFouterInfo = (data: any): IFooter => {
    try {
        const [attachment] = data.attachments;
        const { comet_footer_renderer: fouter } = attachment;
        if (fouter) {
            const fouterTitle =
                fouter.attachment?.title_with_entities?.text ||
                fouter.attachment?.action_links[0]?.link_title;
            const fouterDescription =
                fouter.attachment?.description?.text ||
                fouter.attachment?.action_links[0]?.link_description;
            const fouterSource =
                fouter.source?.text || fouter.attachment?.action_links[0]?.link_display;
            const fouterUrl =
                attachment.style_type_renderer?.attachment?.story_attachment_link_renderer
                    .attachment.web_link.url ||
                fouter.attachment?.story_attachment_link_renderer.attachment.action_links[0].url ||
                fouter.attachment?.action_links[0]?.url;
            return {
                fouterTitle,
                fouterDescription,
                fouterSource,
                fouterUrl,
            };
        }
    } catch (err) {
        log(err, 'getWatchFouterInfo');
        return {
            fouterTitle: '',
            fouterDescription: '',
            fouterSource: '',
            fouterUrl: '',
        };
    }
    return {
        fouterTitle: '',
        fouterDescription: '',
        fouterSource: '',
        fouterUrl: '',
    };
};

const getWatchCallToActionText = (data: any) => {
    const [attachment] = data.attachments;
    try {
        const callToActionText =
            attachment.comet_footer_renderer?.attachment?.call_to_action_renderer?.action_link
                .title;
        return callToActionText;
    } catch (err) {
        log(err, 'getWatchCallToActionText');
        return '';
    }
};

const getWatchCallToActionType = (data: any) => {
    try {
        const [attachment] = data.attachments;
        const callToActionText =
            attachment.comet_footer_renderer?.attachment?.call_to_action_renderer?.action_link
                .link_type;
        return callToActionText;
    } catch (err) {
        log(err, 'getWatchCallToActionType');
        return '';
    }
};

const getWatchPostImage = (data: any): IImage => {
    const [attachment] = data.attachments;
    if (attachment.style_type_renderer) {
        try {
            const media = attachment.style_type_renderer.attachment?.media;

            return getWatchImageUri(media);
        } catch (err) {
            log(err, 'getWatchPostImage');
            return {
                uri: '',
                width: 0,
                height: 0,
            };
        }
    }
    return {
        uri: '',
        width: 0,
        height: 0,
    };
};

const getWatchImageUri = (media: any): IImage => {
    if (!media) {
        return {
            uri: '',
            width: 0,
            height: 0,
        };
    }
    if (media.imageLargeAspect) {
        const { uri, width, height } = media.imageLargeAspect;
        return { uri, width, height };
    } else if (media.imageFlexible) {
        const { uri, width, height } = media.imageFlexible;
        return { uri, width, height };
    } else if (media.imageServerSelected) {
        const { uri, width, height } = media.imageServerSelected;
        return { uri, width, height };
    }
    return {
        uri: '',
        width: 0,
        height: 0,
    };
};

const getWatchPostText = (data: any) => {
    const [attachment] = data.attachments;
    try {
        const postText = data.message?.text || attachment.media.savable_description?.text;
        return postText;
    } catch (err) {
        log(err, 'getWatchPostText');
        return undefined;
    }
};

const getWatchReactionsInfo = (data: any): IReaction => {
    try {
        const reactionsData =
            data.feedback_context.feedback_target_with_context?.top_reactions?.edges ||
            data.feedback_context.feedback_target_with_context.cannot_see_top_custom_reactions
                ?.top_reactions?.edges ||
            [];
        return reactionsData.reduce(
            (map: { [key: string]: number }, reaction: any) => ({
                ...map,
                likes: map.likes + reaction.reaction_count,
                [getReactionKey(reaction.node.id)]: reaction.reaction_count,
            }),
            {
                like: 0,
                anger: 0,
                love: 0,
                likes: 0,
                wow: 0,
                support: 0,
                haha: 0,
                sorry: 0,
            },
        );
    } catch (err) {
        log(err, 'getWatchReactionsInfo');
        return {
            like: 0,
            anger: 0,
            love: 0,
            likes: 0,
            wow: 0,
            support: 0,
            haha: 0,
            sorry: 0,
        };
    }
};

const getWatchCommentsCount = (data: any) => {
    try {
        return (
            data?.feedback_context?.feedback_target_with_context?.total_comment_count ||
            data?.feedback_context?.feedback_target_with_context?.comment_count?.total_count
        );
    } catch (err) {
        log(err, 'getWatchCommentsCount');
        return 0;
    }
};

const getWatchViewsCount = (data: any) => {
    try {
        return data.feedback_context.feedback_target_with_context.video_view_count_renderer
            ?.feedback.video_view_count;
    } catch (err) {
        log(err, 'getWatchViewsCount');
        return 0;
    }
};

const getWatchPostType = (data: any): PostType => {
    const [attachment] = data.attachments;

    if (attachment.media.__typename === 'Video') {
        return PostType.VIDEO;
    }

    switch (attachment.style_type_renderer?.__typename) {
        case 'StoryAttachmentShareStyleRenderer':
        case 'StoryAttachmentShareAdStyleRender':
            return PostType.PHOTO;
        case 'StoryAttachmentVideoStyleRenderer':
            return PostType.VIDEO;
        case 'StoryAttachmentAlbumStyleRenderer':
            return PostType.ALBUM;
        case 'StoryAttachmentMultiShareStyleRenderer':
            return PostType.CAROUSEL;
        case 'StoryAttachmentLeadGenLinkRenderer':
            return PostType.LINK;
        default:
            return PostType.UNKNOWN;
    }
};

const getReactionKey = (id: string) => {
    switch (id) {
        case '1635855486666999':
            return 'like';
        case '1678524932434102':
            return 'love';
        case '115940658764963':
            return 'haha';
        case '613557422527858':
            return 'support';
        case '478547315650144':
            return 'wow';
        case '908563459236466':
            return 'sorry';
        case '444813342392137':
            return 'anger';
        default:
            return '';
    }
};
