import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import findIndex from 'lodash/findIndex';
import _keyBy from 'lodash/keyBy';
import { v4 as uuidv4 } from 'uuid';
import * as Sentry from '@sentry/browser';

import { IAd, PostType } from '../../models';
import { startListening } from '../listenerMiddleware';
import { IAppState } from '..';
import { debug, log } from '../../../scripts/logger';
import Api, { customFetch } from '../../Api';
import { setUserId } from '../settings/reducer';

const REPOST = 2 * 60 * 60 * 1000; //   2h

type FilteredAds = {
    updatedAds: IAd[];
    newAds: IAd[];
    updatedAdsToPost: IAd[];
};

export interface IAds {
    data: IAd[];
    collectedAds: number;
}

export const initialState: IAds = {
    data: [],
    collectedAds: 0,
};

const adsSlice = createSlice({
    name: 'ads',
    initialState,
    reducers: {
        // tslint:disable-next-line: no-empty
        addAds(_state, _action: PayloadAction<IAd[]>) {},
        addAdsSuccess(state, action: PayloadAction<IAd[]>) {
            const newData = action.payload;
            state.data = [...newData, ...state.data];
            state.collectedAds = state.collectedAds + newData.length;
        },
        removeAd(state, action: PayloadAction<IAd>) {
            const dataToRemove = action.payload;
            state.data = state.data.filter((d) => d.postId !== dataToRemove.postId);
        },
        updateAd(state, action: PayloadAction<IAd>) {
            const dataToUpdate = action.payload as IAd;
            const dataClone = [...state.data];
            const index = findIndex(dataClone, { postId: dataToUpdate.postId });
            dataClone.splice(index, 1, dataToUpdate);
            state.data = dataClone;
        },
        removeAllAds(state) {
            state.data = [];
        },
        RemoveAllNonFavoriteAds(state) {
            state.data = state.data.filter(({ favorite }) => !!favorite);
        },
    },
});

export const { addAds, addAdsSuccess, updateAd, removeAd, removeAllAds, RemoveAllNonFavoriteAds } =
    adsSlice.actions;

export default adsSlice.reducer;

function extractUrlFromHTMLNode(htmlString: string) {
    // Regular expression to match document.location.replace with its URL
    const pattern = /document\.location\.replace\(["']([^"']+)["']\)/;

    // Use the regular expression to search for the pattern in the HTML string
    const matches = htmlString.match(pattern);

    // If a match is found, unescape the URL and return it
    if (matches && matches.length > 1) {
        // Unescape forward slashes
        return matches[1].replace(/\\\//g, '/');
    }

    // Return null if no matching pattern was found
    return null;
}

async function fetchUrl(footer: string): Promise<any> {
    try {
        const response = await customFetch(footer);
        const text = await response.text();
        return extractUrlFromHTMLNode(text);
    } catch (err) {
        Sentry.captureException(err, {
            tags: {
                feature: 'fetch_url',
            },
        });
        return footer;
    }
}

async function fetchProduct(url: string) {
    try {
        const urlObj = new URL(url);
        if (!urlObj.pathname.includes('/products/')) {
            return undefined;
        }

        const response = await customFetch(`${urlObj.origin}${urlObj.pathname}.json`);
        const data = await response.json();
        return data?.product;
    } catch (err) {
        Sentry.captureException(err, {
            tags: {
                feature: 'fetch_product',
            },
        });
        return undefined;
    }
}

async function processAds(newPayload: IAd[]) {
    for (const ad of newPayload) {
        if (ad.fouterUrl) {
            if (ad.fouterUrl.startsWith('https://l.facebook.com/l.php')) {
                ad.fouterUrl = await fetchUrl(ad.fouterUrl);
            }
            ad.product = await fetchProduct(ad.fouterUrl);
        }

        if (ad.postType === PostType.CAROUSEL) {
            for (const card of ad.carouselCardsInfos) {
                if (card.actionUrl) {
                    const cardFooter = card.actionUrl;
                    if (cardFooter.startsWith('https://l.facebook.com/l.php')) {
                        card.actionUrl = await fetchUrl(cardFooter);
                    }

                    card.product = await fetchProduct(card.actionUrl);
                }
            }
        }
    }
}

startListening({
    actionCreator: addAds,
    effect: async (action, listenerApi) => {
        try {
            const version = chrome.runtime.getManifest().version;
            const state = listenerApi.getState() as IAppState;
            const data = state.ads.data;
            let userId = state.settings.userId;
            const now = new Date().getTime();

            const stateAdsMap: { [key: string]: IAd } = _keyBy(data, 'postId');

            const newPayload = action.payload as IAd[];

            await processAds(newPayload);

            const { updatedAds, newAds, updatedAdsToPost } = newPayload.reduce(
                (acc: FilteredAds, curr: IAd) => {
                    const oldAd = stateAdsMap[curr.postId];
                    if (!oldAd) {
                        return {
                            ...acc,
                            newAds: [curr, ...acc.newAds],
                        };
                    } else {
                        const lastSeen = new Date(oldAd.addedAt).getTime();
                        if (lastSeen + REPOST < now) {
                            return {
                                ...acc,
                                updatedAdsToPost: [
                                    {
                                        ...oldAd,
                                        ...curr,
                                    },
                                    ...acc.updatedAdsToPost,
                                ],
                            };
                        }

                        return {
                            ...acc,
                            updatedAds: [
                                {
                                    ...oldAd,
                                    ...curr,
                                },
                                ...acc.updatedAds,
                            ],
                        };
                    }
                },
                {
                    updatedAds: [],
                    newAds: [],
                    updatedAdsToPost: [],
                },
            );

            if (newAds.length > 0) {
                listenerApi.dispatch(
                    addAdsSuccess(
                        newAds.map((ad) => ({
                            ...ad,
                            postData: undefined,
                        })),
                    ),
                );
            }

            if (updatedAds.length > 0) {
                updatedAds.forEach((ad) =>
                    listenerApi.dispatch(
                        updateAd({
                            ...ad,
                            postData: undefined,
                        }),
                    ),
                );
            }

            if (updatedAdsToPost.length > 0) {
                updatedAdsToPost.forEach((ad) =>
                    listenerApi.dispatch(
                        updateAd({
                            ...ad,
                            postData: undefined,
                        }),
                    ),
                );
            }

            if (!userId) {
                userId = uuidv4();
                listenerApi.dispatch(setUserId(userId));
            }

            const body = [...newAds, ...updatedAdsToPost].map((ad) => ({
                ...ad,
                version,
                userId,
            }));
            if (body.length > 0) {
                if (process.env.NODE_ENV === 'development') {
                    debug(body, 'NEW_POSTED_ADS');
                } else {
                    try {
                        await Api.saveData(JSON.stringify(body));
                    } catch (err) {
                        Sentry.captureException(err, {
                            extra: {
                                posts: body,
                            },
                            tags: {
                                feature: 'save_posts',
                            },
                        });
                        log(err, 'onAdd');
                    }
                }
            }
        } catch (err) {
            Sentry.captureException(err, {
                tags: {
                    feature: 'on_add',
                },
            });
            log(err, 'onAdd');
        }
    },
});
