export interface ICarrousel {
    text: string;
    imageUrl: string;
    imageWidth: number;
    imageHeight: number;
    video: IVideo;
    type: 'image' | 'video';
    description: string;
    actionUrl: string;
    actionTitle: string;
    product?: Product;
}

export interface IPage {
    pageName: string;
    pageId: string;
    pageLogo: string;
    pageUrl: string;
    adLibraryId: string;
    pageCategory?: string;
}

export interface IVideo {
    videoHdUrl: string;
    videoId: string;
    videoUrl: string;
    videoWidth: number;
    videoHeight: number;
    thumbnailImage: string;
}

export interface IFooter {
    fouterTitle: string;
    fouterDescription: string;
    fouterSource: string;
    fouterUrl: string;
}

export interface IReaction {
    likes: number;
    like: number;
    sorry: number;
    anger: number;
    love: number;
    wow: number;
    support: number;
    haha: number;
}

export enum PostType {
    VIDEO = 'video',
    ALBUM = 'album',
    PHOTO = 'photo',
    LINK = 'link',
    CAROUSEL = 'carousel',
    UNKNOWN = 'unknown',
    TEXT = 'text',
}

export interface Product {
    id: number;
    title: string;
    body_html: string;
    vendor: string;
    product_type: string;
    created_at: string;
    handle: string;
    updated_at: string;
    published_at: string;
    template_suffix: string;
    published_scope: string;
    tags: string;
    variants: ProductVariant[];
    options: ProductOption[];
    images: ProductImage[];
    image: ProductImage;
}

export interface ProductVariant {
    id: number;
    product_id: number;
    title: string;
    price: string;
    sku: string;
    position: number;
    compare_at_price: string;
    fulfillment_service: string;
    inventory_management: string;
    option1: string;
    option2: any;
    option3: any;
    created_at: string;
    updated_at: string;
    taxable: boolean;
    barcode: string;
    grams: number;
    image_id: any;
    weight: number;
    weight_unit: string;
    requires_shipping: boolean;
    quantity_rule: QuantityRule;
    quantity_price_breaks: any[];
}

export interface QuantityRule {
    min: number;
    max: any;
    increment: number;
}

export interface ProductOption {
    id: number;
    product_id: number;
    name: string;
    position: number;
    values: string[];
}

export interface ProductImage {
    id: number;
    product_id: number;
    position: number;
    created_at: string;
    updated_at: string;
    alt: any;
    width: number;
    height: number;
    src: string;
    variant_ids: any[];
}

export interface IAd extends IVideo, IPage, IFooter, IReaction {
    postData: any;
    postId: string;
    postText: string;
    callToActionText: string;
    callToActionType: string;
    commentsCount: number;
    shareCount: number;
    viewsCount: number;
    postType: PostType;
    carouselCardsInfos: ICarrousel[];
    postImage: string;
    postImageWidth: number;
    postImageHeight: number;
    postAlbumImages: string[];
    publishedAt: string;
    source: string;
    addedAt: string;
    favorite?: boolean;
    gr?: number;
    product?: Product;
}

export interface IMessage {
    type: string;
    payload: any;
}

export interface ExportAd {
    Likes: number;
    Like: number;
    Sorry: number;
    Anger: number;
    Love: number;
    Wow: number;
    Haha: number;
    Comments: number;
    Shares: number;
    Views: number | string;
    Pagelink: string;
    PageName: string;
    Text: string;
    Type: string;
    Postlink: string;
}
