import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import * as Sentry from '@sentry/browser';
import { v4 as uuidv4 } from 'uuid';

import { importMessages, LocaleMessages, Locales, SupportedLocales } from 'ts/helpers';
import { log } from 'ts/scripts/logger';
import { refreshFacebookTabs } from 'ts/scripts/utils';
import enJSON from 'lang/en.json';

import { startListening } from '../listenerMiddleware';

import { IAppState } from '..';

const GA_ID = 'UA-161441290-1';

export interface IAppSettings {
    rated: boolean;
    followedInstagram: boolean;
    autoScroll: boolean;
    organicPosts: boolean;
    userId: string;
    freshchatRestoreId?: string;
    locale: SupportedLocales;
    messages: LocaleMessages;
    firstRun: boolean;
}

export const initialState: IAppSettings = {
    rated: false,
    followedInstagram: false,
    autoScroll: false,
    organicPosts: true,
    userId: uuidv4(),
    locale: 'en',
    messages: enJSON,
    firstRun: true,
};

const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        setRated(state) {
            state.rated = true;
        },
        setFollowedInstagram(state) {
            state.followedInstagram = true;
        },
        setUserId(state, action: PayloadAction<string>) {
            state.userId = action.payload;
        },
        setFreshchatRestoreId(state, action: PayloadAction<string>) {
            state.freshchatRestoreId = action.payload;
        },
        enableAutoScroll(state) {
            state.autoScroll = true;
        },
        disableAutoScroll(state) {
            state.autoScroll = false;
        },
        hideOrganicPosts(state) {
            state.organicPosts = false;
        },
        showOrganicPosts(state) {
            state.organicPosts = true;
        },
        // tslint:disable-next-line: no-empty
        setLocale(_state, _action: PayloadAction<SupportedLocales>) {},
        setLocaleSuccess(
            state,
            action: PayloadAction<{
                locale: SupportedLocales;
                messages: LocaleMessages;
            }>,
        ) {
            const { locale, messages } = action.payload;
            state.locale = locale;
            state.messages = messages;
        },
        disableFirstRun(state) {
            state.firstRun = false;
        },
        trackEvent: (
            _state,
            _action: PayloadAction<{
                category: string;
                action: string;
                label?: string;
            }>,
            // tslint:disable-next-line: no-empty
        ) => {},
        // tslint:disable-next-line: no-empty
        trackPageView: (_state, _action: PayloadAction<string>) => {},
    },
});

export const {
    setRated,
    setFollowedInstagram,
    setUserId,
    setFreshchatRestoreId,
    enableAutoScroll,
    disableAutoScroll,
    hideOrganicPosts,
    showOrganicPosts,
    setLocale,
    setLocaleSuccess,
    disableFirstRun,
    trackEvent,
    trackPageView,
} = settingsSlice.actions;

export default settingsSlice.reducer;

startListening({
    actionCreator: setLocale,
    effect: async (action, listenerApi) => {
        try {
            const locale = action.payload;
            const messages: LocaleMessages = importMessages(locale as Locales);
            listenerApi.dispatch(
                setLocaleSuccess({
                    locale,
                    messages,
                }),
            );
        } catch (err) {
            Sentry.captureException(err, {
                tags: {
                    feature: 'set_locale',
                },
            });
            log(err, 'setLocaleSuccess');
        }
    },
});

startListening({
    actionCreator: hideOrganicPosts,
    effect: () => {
        refreshFacebookTabs();
    },
});

startListening({
    actionCreator: showOrganicPosts,
    effect: () => {
        refreshFacebookTabs();
    },
});

startListening({
    actionCreator: trackPageView,
    effect: ({ payload }, listenerApi) => {
        try {
            const state = listenerApi.getState() as IAppState;
            const { userId } = state.settings;

            const gPayload: Record<string, string> = {
                v: '1',
                t: 'pageview',
                tid: GA_ID,
                cid: userId,
                dh: location.hostname,
                dp: `${payload}`,
            };
            fetch('https://www.google-analytics.com/collect', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: new URLSearchParams(gPayload).toString(),
            })
                .then((response) => {
                    if (!response.ok) {
                        throw new Error(`HTTP error! status: ${response.status}`);
                    }
                })
                .catch((error) => {
                    Sentry.captureException(error, {
                        tags: {
                            feature: 'track_page_view',
                        },
                    });
                    log(error, 'onTrackPageView');
                });
        } catch (err) {
            Sentry.captureException(err, {
                tags: {
                    feature: 'track_page_view',
                },
            });
            log(err, 'onTrackPageView');
        }
    },
});

startListening({
    actionCreator: trackEvent,
    effect: ({ payload }, listenerApi) => {
        const state = listenerApi.getState() as IAppState;
        const { userId } = state.settings;
        const { category, action, label } = payload;

        let gPayload: Record<string, string> = {
            v: '1',
            t: 'event',
            tid: GA_ID,
            cid: userId,
            ec: category,
            ea: action,
        };
        if (label) {
            gPayload = {
                ...gPayload,
                el: label,
            };
        }
        fetch('https://www.google-analytics.com/collect', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body: new URLSearchParams(gPayload).toString(),
        })
            .then((response) => {
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
            })
            .catch((error) => {
                Sentry.captureException(error, {
                    tags: {
                        feature: 'track_event',
                    },
                });
                log(error, 'onTrackEvent');
            });
    },
});
