import { warn } from '../scripts/logger';

const API_URL = 'https://api.myadfinder.com';
const MAX_RETRIES = 3;
const RETRY_DELAY = 3 * 1000; // 3 seconds

/**
 * Custom fetch wrapper with retry mechanism.
 *
 * @param url - The URL to fetch.
 * @param options - The options for fetch.
 * @param maxRetries - Maximum number of retry attempts.
 * @param retryDelay - Delay between retries in milliseconds.
 * @returns - The fetch response.
 */
export async function customFetch(
    url: string,
    options: RequestInit = {},
    maxRetries = MAX_RETRIES,
    retryDelay = RETRY_DELAY,
): Promise<Response> {
    let attempts = 0;

    while (attempts < maxRetries) {
        try {
            const response = await fetch(url, options);

            if (!response.ok) {
                throw new Error(`Fetch failed with status: ${response.status}`);
            }

            return response; // Return the response if successful.
        } catch (error: any) {
            attempts += 1;
            warn(`Attempt ${attempts} failed: ${error.message}`);

            if (attempts >= maxRetries) {
                throw new Error(`Failed to fetch ${url} after ${maxRetries} attempts`);
            }

            await new Promise((resolve) => setTimeout(resolve, retryDelay)); // Delay before next retry.
        }
    }

    // This line should technically never be reached because an error is thrown after max retries.
    throw new Error('Unexpected error: customFetch reached an unreachable state.');
}

const saveData = (body: any) =>
    customFetch(`${API_URL}/products/posts`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body,
    }).then((response) => response.json());

export default {
    saveData,
};
