// "import type" ensures en messages aren't bundled by default
import * as sourceOfTruth from 'lang/en.json';
// Note: in order to use "import type" you'll need Babel >= 7.9.0 and/or TypeScript >= 3.8.
// Otherwise, you can use a normal import and accept to always bundle one language + the user required one
import * as en from 'lang/en.json';
import * as fr from 'lang/fr.json';
import * as pt from 'lang/pt.json';
import * as de from 'lang/de.json';
import * as es from 'lang/es.json';
import * as it from 'lang/it.json';
import * as vi from 'lang/vi.json';
import * as th from 'lang/th.json';
import { warn } from 'ts/scripts/logger';

export type LocaleMessages = typeof sourceOfTruth;
export enum Locales {
    es = 'es',
    en = 'en',
    fr = 'fr',
    pt = 'pt',
    de = 'de',
    it = 'it',
    vi = 'vi',
    th = 'th',
}
export type SupportedLocales = keyof typeof Locales;

const serializeMessages = <T extends Record<string, any>>(
    messages: T | null | undefined,
): Record<string, string> => {
    if (!messages) return {};

    return Object.keys(messages).reduce(
        (acc, key) => {
            try {
                const value = messages[key];
                acc[key] = value?.toString() || ''; // Handle cases where `toString` returns null or undefined
            } catch (e) {
                warn(`Failed to serialize message for key: ${key} ${e}`);
                acc[key] = '';
            }
            return acc;
        },
        {} as Record<string, string>,
    );
};

// return type on this signature enforces that all languages have the same translations defined
export function importMessages(locale: Locales): LocaleMessages {
    let localeMessages: LocaleMessages;

    switch (locale) {
        case Locales.en:
            localeMessages = en;
            break;
        case Locales.fr:
            localeMessages = fr;
            break;
        case Locales.pt:
            localeMessages = pt;
            break;
        case Locales.de:
            localeMessages = de;
            break;
        case Locales.es:
            localeMessages = es;
            break;
        case Locales.it:
            localeMessages = it;
            break;
        case Locales.vi:
            localeMessages = vi;
            break;
        case Locales.th:
            localeMessages = th;
            break;
        default:
            localeMessages = en;
            break;
    }

    return serializeMessages(localeMessages) as LocaleMessages;
}
