export const log = (msg: any, messageId?: string) => {
    if (process.env.NODE_ENV === 'development') {
        // tslint:disable-next-line: no-console
        console.log(messageId || '[error]:', msg);
    }
};

export const debug = (msg: any, messageId?: string) => {
    if (process.env.NODE_ENV === 'development') {
        // tslint:disable-next-line: no-console
        console.debug(messageId || '[debug]:', msg);
    }
};

export const warn = (err: any, messageId?: string) => {
    if (process.env.NODE_ENV === 'development') {
        // tslint:disable-next-line: no-console
        console.warn(messageId || '[warn]:', err);
    }
};
